:root {
  --black: #000;
  --not-so-black: #121212;
  --white: #fff;
  --not-so-white: #fcfcfc;
  --white-text: #fcfcfc;
  --grey: #ababab;
  --yellow: #f8d34f;
  --yellow-form-bg: #f8d34f1a;
  --pastel: #84a178;
  --form-border: #0a8a17;
  --form-bg: #f8d34f1a;
  --btn-hover: #0a8a17;
  --iceream-theme: #9bbe8e;
  --milkshake-theme: #d5936c;
  --outline-1: red 2px dashed;
  --outline-2: green 2px dashed;
}

html, div, span, object, iframe, p, blockquote, pre, abbr, acronym, address, big, cite, code, del, dfn, em, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, i, center, dl, dt, dd, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

ul, ol, menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  box-sizing: border-box;
  overflow: auto;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: #121212;
  background-color: var(--pastel);
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  text-decoration: none;
}

